import { Grid, Sidebar, Toast } from "@taroify/core"
import * as React from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function EventSidebar() {
  return (
    <>
      <Toast id="toast" />
      <Sidebar onChange={(newValue, { children }: Sidebar.TabObject) => Toast.open(children)}>
        <Sidebar.Tab> 1</Sidebar.Tab>
        <Sidebar.Tab> 2</Sidebar.Tab>
        <Sidebar.Tab> 3</Sidebar.Tab>
      </Sidebar>
    </>
  )
}

export default function SidebarDemo() {
  return (
    <Page title="Sidebar " className="sidebar-demo">
      <Grid columns={2} centered bordered={false}>
        <Grid.Item>
          <Block title="">
            <Sidebar>
              <Sidebar.Tab></Sidebar.Tab>
              <Sidebar.Tab></Sidebar.Tab>
              <Sidebar.Tab></Sidebar.Tab>
            </Sidebar>
          </Block>
        </Grid.Item>
        <Grid.Item>
          <Block title="">
            <Sidebar>
              <Sidebar.Tab badge></Sidebar.Tab>
              <Sidebar.Tab badge="5"></Sidebar.Tab>
              <Sidebar.Tab badge="20"></Sidebar.Tab>
            </Sidebar>
          </Block>
        </Grid.Item>
        <Grid.Item>
          <Block title="">
            <Sidebar>
              <Sidebar.Tab></Sidebar.Tab>
              <Sidebar.Tab disabled></Sidebar.Tab>
              <Sidebar.Tab></Sidebar.Tab>
            </Sidebar>
          </Block>
        </Grid.Item>
        <Grid.Item>
          <Block title="">
            <EventSidebar />
          </Block>
        </Grid.Item>
      </Grid>
    </Page>
  )
}
